	module.exports = {"data":{"post":{"html":"<p>Meanwhile, Ronald Barnabas Schill, Hamburg's ex-Judge Merciless and current Interior Senator,  seems to have delusions of national political grandeur. He's offering Edmund Stoiber to enter the Schill Party in the Bundestag elections as an ally to pick up votes from northern conservatives who might be unwilling to vote for a Bavarian. That's the rumor, anyway. The CSU is doing their best to ignore the supposed offer.</p>","fields":{"title":"Delusions Of Grandeur","date":"22 Jan 2002","url":"https://papascott.de/2002-01-22-delusions-of-grandeur/"}}},"pathContext":{"slug":"/2002-01-22-delusions-of-grandeur/","prev":{"fields":{"title":"Temelin: The Signatures Are In","date":"21 Jan 2002","slug":"/2002-01-21-temelin-the-signatures-are-in/"}},"next":{"fields":{"title":"In This Corner... And In This One","date":"22 Jan 2002","slug":"/2002-01-22-in-this-corner-and-in-this-one/"}}}}

